export default {
  name: "Footer",
  data() {
    return {
      modalActive: false,
    }
  },
  mounted() {
    this.modalActive = false;
  },
  props: {
    options: {
      type: Object,
      default: () => ({
        text: 'Default',
        color: '#fff',
        bgcolor: '#3c3c3c',
      }),
    },
  },
  methods:{
    DialogMethod(params) {
      this.$emit("OptionWater", params);
    },
    toggleFilter(modalTarget) {
      if (this.modalActive) {
        this.modalActive = false;
        this.$bvModal.hide(modalTarget);
      } else {
        this.modalActive = true;
        this.$bvModal.show(modalTarget);
      }
    },
  }
};
